:root {
    --section-overlay: rgba(0, 0, 0, 0.7);
    --background-overlay: rgba(0, 0, 0, 0.2);
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.App{
    width: 100%;
    height: 100vh;
    background-position:top;
    background-size: cover;
}

.overlay{
    width: 100%;
    height: 100vh;
    background-color: var(--background-overlay);
}

.container{
    max-width: 800px;
    margin: auto;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    padding: 1rem;
}

/* top */

.section{
    width: 100%;
    padding: 1.5rem;
    border-radius: .8rem;
    color: #fff;
}

.section__inputs, .section__temperature{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--section-overlay);
}


.section__inputs>input{
    border: 0.8px solid white;
    border-radius: 0.8rem;
    background-color: transparent;
    color: white;
    padding: 0.5rem;
    font-size: 20px;
    font-weight: 200;
}

.section__inputs>input:focus{
    outline: none;
}

.section__inputs>button{
    background-color: #fff;
    padding:10px 50px;
    font-size: 20px;
    font-weight: 500;
    border-radius: 1rem;
    border: none;
}

.section__inputs>button:hover{
    cursor: pointer;
    background-color: lightgray;
}


.icon{
    display: flex;
    flex-direction: column;
    align-items:center ;
    justify-content: center;
}

.icon>h3{
    font-size: 15px;
    font-weight: 200;
    text-transform: capitalize;
}

.temperature>h1{
    font-size: 60px;
}
